body {
  text-align: center;
  background-color: #fff;
  padding: 0 15px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #569ff7;
  border: 1px solid #569ff7;
  border-radius: 4px;
  padding: 6px 22px;
  font-size: 20px;
}

button[disabled] {
  color: #4d4b4b;
  background-color: #ccc;
  border-color: #ccc;
}

#datetime-picker {
  text-align: center;
  color: #393939;
  border: 1px solid #569ff7;
  border-radius: 4px;
  margin-right: 20px;
  padding: 6px 22px;
  font-size: 20px;
}

#datetime-picker[disabled] {
  color: #4d4b4b;
  background-color: #ccc;
  border-color: #ccc !important;
}

.timer {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.field {
  color: #393939;
  min-width: 150px;
  border: 1px solid #569ff7;
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6px 22px;
  font-size: 20px;
  display: flex;
}

.field:not(:last-child) {
  margin-right: 20px;
}

.value {
  font-size: 30px;
  font-weight: 700;
}

